'use client';

import { cookies } from 'next/headers';
import { useEffect } from 'react';

type Props = {
  flash: string;
};

export default function ToastProvider({ flash }: Props) {
  useEffect(() => {
    const run = async () => {
      const cookieStore = await cookies();
      const flash = await cookieStore.get('flash');
    };
    run();
  }, []);

  return <div>{flash}</div>;
}
